import React, { Component } from "react";

// component drumpad

import DrumPad from "./DrumPad";
import DrumPadDisable from "./DrumPadDisable";

export default class DrumBank extends Component {
  drumDisable = this.props.drumBank.map((sound) => {
    return (
      <DrumPadDisable
        key={sound.id}
        clipId={sound.id}
        keyTrigger={sound.keyTrigger}
        keyCode={sound.keyCode}
      />
    );
  });

  render() {
    const drumLayout =
      this.props.drumBank !== null ? (
        this.props.drumBank.map((sound) => {
          return (
            <DrumPad
              key={sound.id}
              clipId={sound.id}
              keyTrigger={sound.keyTrigger}
              keyCode={sound.keyCode}
              source={sound.url}
              setDisplay={this.props.setDisplay}
            />
          );
        })
      ) : (
        <div>No Bank selected</div>
      );

    if (this.props.power) {
      return <div className="drumbank-style">{drumLayout}</div>;
    } else {
      return <div className="drumbank-style">{this.drumDisable}</div>;
    }
  }
}
