import React, { Component } from "react";

export default class DrumPad extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeState: false,
    };
    this.handlePressKey = this.handlePressKey.bind(this);
    this.playSound = this.playSound.bind(this);
    this.changeStyle = this.changeStyle.bind(this);
  }

  componentDidMount() {
    document.addEventListener("keydown", this.handlePressKey);
  }
  componentWillUnmount() {
    document.removeEventListener("keydown", this.handlePressKey);
  }

  handlePressKey(e) {
    if (e.keyCode === this.props.keyCode) {
      this.playSound();
    }
  }

  playSound() {
    const sound = document.getElementById(this.props.keyTrigger);
    sound.currentTime = 0;
    sound.play();
    this.changeStyle();
    this.props.setDisplay(this.props.clipId.replace(/-/g, " "));
    setTimeout(() => {
      this.changeStyle();
    }, 100);
  }

  changeStyle() {
    this.setState({ activeState: !this.state.activeState });
  }

  render() {
    const drumpadStyle = this.state.activeState ? "drumpad active" : "drumpad";
    return (
      <div
        id={this.props.clipId}
        onClick={this.playSound}
        className={drumpadStyle}
      >
        <audio
          className="clip"
          id={this.props.keyTrigger}
          src={this.props.source}
        />
        {this.props.keyTrigger}
      </div>
    );
  }
}
