import React, { Component } from "react";
import { bankOne, bankTwo } from "../utils/utils";

import DrumBank from "./DrumBank";

export default class DrumMachine extends Component {
  constructor(props) {
    super(props);
    this.state = {
      power: true,
      display: String.fromCharCode(160),
      currentPadBank: bankOne,
      currentPadBankId: "Heater Kit",
      slider_volumen: 0.3,
    };
    this.setPower = this.setPower.bind(this);
    this.setDisplay = this.setDisplay.bind(this);
    this.setVolumen = this.setVolumen.bind(this);
    this.selectBank = this.selectBank.bind(this);
    this.clearDisplay = this.clearDisplay.bind(this);
  }

  setPower() {
    this.setState({
      power: !this.state.power,
      display: String.fromCharCode(160),
    });
  }

  selectBank() {
    if (this.state.power) {
      if (this.state.currentPadBankId === "Heater Kit") {
        this.setState({
          currentPadBank: bankTwo,
          display: "Smooth Piano Kit",
          currentPadBankId: "Smooth Piano Kit",
        });
      } else {
        this.setState({
          currentPadBank: bankOne,
          display: "Heater Kit",
          currentPadBankId: "Heater Kit",
        });
      }
    }
  }

  setDisplay(name) {
    if (this.state.power) {
      this.setState({ display: name });
    }
  }

  setVolumen(e) {
    if (this.state.power) {
      const volumen = e.target.value;
      this.setState({
        slider_volumen: volumen,
        display: "Volumen: " + Math.round(volumen * 100),
      });

      setTimeout(() => this.clearDisplay(), 1000);
    }
  }

  clearDisplay() {
    if (this.state.power) {
      this.setState({ display: String.fromCharCode(160) });
    }
  }

  render() {
    const currentBank =
      this.state.currentPadBankId === "Heater Kit"
        ? "bankswitch"
        : "bankswitch active";

    const powerButtonStyle = this.state.power
      ? "on-button active"
      : "on-button";

    const buttonText = this.state.power ? "ON" : "OFF";

    const clipVolumen = [].slice.call(document.getElementsByClassName("clip"));

    clipVolumen.forEach((clip) => {
      clip.volume = this.state.slider_volumen;
    });

    return (
      <div className="drum-container">
        <h1>Drum Machine</h1>
        <div className="option-container">
          <DrumBank
            drumBank={this.state.currentPadBank}
            setDisplay={this.setDisplay}
            power={this.state.power}
          />
          <div className="panel-options">
            <div>Power</div>
            <button className={powerButtonStyle} onClick={this.setPower}>
              {buttonText}
            </button>
            <div className="drum-display">{this.state.display}</div>
            <div>Volumen</div>
            <input
              type="range"
              name="volume"
              max="1"
              min="0"
              step="0.01"
              value={this.state.slider_volumen}
              onChange={this.setVolumen}
            />
            <div>Bank Switch</div>
            <div>
              <div className="bank-switch-container" onClick={this.selectBank}>
                <div className={currentBank}></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
